/* CHAT STYLES */
* {
    font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
      Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji,
      Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
    letter-spacing: 0.5px;
  }
  
  .ce-chat-list {
    background-color: rgb(34, 53, 64) !important;
  }
  
  .ce-chats-container {
    background-color: rgb(34, 53, 64) !important;
  }
  
  .ce-active-chat-card {
    background-color: #5286e7 !important;
    border: 4px solid #33c983 !important;
    border-radius: 0px !important;
  }
  
  .ce-chat-subtitle-text {
    color: #c9c9c9 !important;
  }
  
  .ce-chat-form-container {
    padding-bottom: 20px !important;
  }
  
  .ce-text-input {
    border-radius: 6px !important;
    border: 1px solid #cbc2c2 !important;
  }
  
  .ce-primary-button {
    border-radius: 6px !important;
    background-color: #10325b !important;
    position: relative;
    bottom: 1px;
  }
  
  .ce-danger-button {
    background-color: rgb(0, 0, 0) !important;
    border-radius: 22px !important;
  }
  
  .ce-settings {
    background-color: rgb(34, 53, 64) !important;
  }
  
  .ce-autocomplete-input {
    border-radius: 6px !important;
    border: 1px solid #0aa3e4 !important;
  }
  
  .ce-autocomplete-options {
    border-radius: 6px !important;
    border: 1px solid rgb(8, 82, 104) !important;
    background-color: rgb(0, 0, 0) !important;
  }
  
  .ce-chat-settings-container {
    padding-top: 12px !important;
  }
  
  .ce-chat-avatars-row {
    display: none !important;
  }
  
  /* CUSTOM FEED */
  
  .chat-feed {
    height: 100%;
    width: 100%;
    overflow: scroll;
    background-color: rgb(23, 24, 24);
  }
  
  .chat-title-container {
    width: calc(100% - 36px);
    padding: 18px;
    text-align: center;
  }
  
  .chat-title {
    color: rgb(8, 82, 104);
    font-weight: 800;
    font-size: 24px;
  }
  
  .chat-subtitle {
    color: rgb(8, 82, 104);
    font-weight: 600;
    font-size: 12px;
    padding-top: 4px;
  }
  
  .message-row {
    float: left;
    width: 100%;
    display: flex;
    margin-left: 18px;
 
  }
  
  .message-block {
    width: 100%;
    display: inline-block;
    
  }
  
  .message-avatar {
    width: 44px;
    height: 44px;
    border-radius: 22px;
    color: rgb(8, 82, 104);
    text-align: center;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 48px;
  }
  
  .message {
    padding: 12px;
    font-size: 16px;
    border-radius: 6px;
    max-width: 60%;
    background-color: rgb(34, 53, 64) !important;
    
  }
  
  .message-image {
    margin-right: 18px;
    object-fit: cover;
    border-radius: 6px;
    height: 30vw;
    /* width: 30vw; */
    max-height: 200px;
    max-width: 200px;
    min-height: 100px;
    min-width: 100px;
  }
  
  .read-receipts {
    position: relative;
    bottom: 7px;
  }
  
  .read-receipt {
    width: 13px;
    height: 13px;
    border-radius: 13px;
    margin: 1.5px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 14px;
  }
  
  .message-form-container {
    position: absolute;
    bottom: 0px;
    width: calc(100% - 11.5px);
    padding: 18px;
    background-color: rgb(34, 53, 64);
  }
  
  .message-form {
    overflow: hidden;
    border-radius: 6px;
    border: 1px solid #0a89b4;
    background-color: rgb(22, 150, 201);
  }
  
  .message-input {
    height: 40px;
    width: calc(100% - 132px);
    background-color: rgb(41, 41, 41);
    border: 1px solid rgb(9, 80, 138);
    padding: 0px 18px;
    outline: none;
    font-size: 19px;
    color: rgb(255,255,255) !important;
  }
  
  .image-button {
    cursor: pointer;
    padding: 0px 12px;
    height: 100%;
  }
  
  .send-button {
    height: 39px;
    background-color: rgb(22, 150, 201);
    border: 1px solid rgb(22, 150, 201);
    padding: 0px 12px;
    cursor: pointer;
  }
  
  .send-icon {
    top: 1px;
    position: relative;
    transform: rotate(0deg);
    color:rgb(0, 0, 0);
  }
  
  .picture-icon {
    top: 1px;
    position: relative;
    font-size: 14px;
  }
  





  
  /* FORM STYLES */
  *,
  *::after,
  *::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 62,5%;
  }
  
  .wrapper {
    height: 100vh;
      width: 100%;
    background: rgb(166, 73, 3);
    background: linear-gradient(90deg, rgb(4, 55, 70) 7%, rgb(6, 60, 80) 17%, rgb(8, 82, 104) 29%, rgb(8, 82, 104) 44%, rgb(8, 82, 104) 66%, rgb(8, 82, 104) 83%, rgb(6, 60, 80) 96%, rgb(4, 55, 70) 100%, rgb(4, 55, 70) 100%);  
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .input {
    color: rgb(207, 198, 198);
    font-size: 1.2rem;
    margin: 0 auto;
    padding: 1.5rem 2rem;
    border-radius: 0.2rem;
    background-color: rgb(34, 34, 34);
    border: none;
    width: 90%;
    display: block;
    border-bottom: 0.3rem solid transparent;
    transition: all 0.3s;
    outline: none;
    margin-bottom: 25px;
  }
  
  .form {
    width: 400px;
  }
  
  .title {
    text-align: center;
    color: rgb(205, 142, 25);
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
    margin-bottom: 30px;
    width: 100%;
    font-size: 2.3em;
  }
  
  .button {
    border-radius: 4px;
    border: none;
    background: linear-gradient(90deg, rgba(117,84,160,1) 7%, rgba(117,84,160,1) 17%, rgba(106,95,168,1) 29%, rgba(99,103,174,1) 44%, rgba(87,116,184,1) 66%, rgba(70,135,198,1) 83%, rgba(44,163,219,1) 96%, rgba(22,188,237,1) 100%, rgba(0,212,255,1) 100%);
    color: rgb(0, 0, 0);
    text-align: center;
    text-transform: uppercase;
    font-size: 22px;
    padding: 20px;
    width: 200px;
    transition: all 0.4s;
    cursor: pointer;
    margin: 5px;
    width: 90%;
  }
  .button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.4s;
  }
  .button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  .button:hover span {
    padding-right: 25px;
  }
  .button:hover span:after {
    opacity: 1;
    right: 0;
  }
  
  .error {
    color: rgb(239, 93, 8);
    text-align: center;
    margin-top: 20px;
  }